export default {
	data() {
		return {
			carList: [], // 车辆列表
		};
	},
	methods: {

		// 删除车辆
		remove(id, carnumber) {
			uni.showModal({
				title: '提示',
				content: `是否要删除车牌为:${carnumber}`,
				success: async (res) => {
					if (res.confirm) {
						let deleteCarInfo = await this.ajax(this.api.deleteCarInfo, {
							id
						}, 'POST')
						if (deleteCarInfo.data.code) {
							uni.showToast({
								icon: 'none',
								title: deleteCarInfo.data.msg,
								duration: 2000
							})
							this.getCarList()
						}

					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},

		async getCarList() {
			let carList = await this.ajax(this.api.carList)
			if (carList.data.code == 200) {
				if (carList.data.data != 'null') {
					this.carList = carList.data.data
				}
			}
		},


		// 添加车辆
		addcar() {
			uni.navigateTo({
				url: '../addcar/addcar'
			})
		}
	},

	onShow() {
		this.getCarList()
	}
}
